import React, { useEffect } from 'react';
import { useDispatch, useSelector } from "react-redux";
import Final from './components/Final';
import Loading from './components/Loading';
import Main from './components/Main';
import Start from './components/Start';

import Overlay from './panels/Overlay';
import ServerConnect from './service';

import Button from './components/Button';
import Text from './components/Text';
import Title from './components/Title';
import { setActiveModal, setActivePanel, setBackgrounds, setInfo, setQuiz, setQuizIndex, setUserID } from './store/mainReducer';
import Compet from './components/Compet';


const App = () => {
  const dispatch = useDispatch()
  const activePanel = useSelector(state => state.main.activePanel)
  const activeModal = useSelector(state => state.main.activeModal)
  const answerIsRight = useSelector(state => state.main.answerIsRight)
  const userID = useSelector(state => state.main.userID)
  const quiz = useSelector(state => state.main.quiz)
  const quizIndex = useSelector(state => state.main.quizIndex)
  const info = useSelector(state => state.main.info)

  console.log(quiz)

  const sendStat = (stat) => {
    ServerConnect.sendPost('/api/statistics', {"name": stat, "user_id" : userID})
    .then(data=>console.log(data))
    .catch(error=>console.log(error))
  }

  const goToNextQuestion = () => {
    if(quizIndex+1 === quiz.length){
      dispatch(setActivePanel('final'))
      dispatch(setActiveModal(null))
    } else {
      dispatch(setQuizIndex(quizIndex+1))
      dispatch(setActiveModal(null))
    }
  }

  const parseQuery = (queryString) => {
    let query = {};
    let pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
    for (let i = 0; i < pairs.length; i++) {
        let pair = pairs[i].split('=');
        query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || '');
    }
    return query;
  }

  const imageUrlToBase64 = async (url) => {
    const data = await fetch(url);
    const blob = await data.blob();
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(blob);
      reader.onloadend = () => {
        const base64data = reader.result;
        resolve(base64data);
      };
      reader.onerror = reject;
    });
  };

  useEffect(()=>{
    ServerConnect.sendGet('/api/all_files')
    .then(data=>{
      const quiz = data.filter(item=>item.type === 'quiz')

      let quizPromises = [];
      let bgPromises = [];
      let infoPromises = [];

      quiz.forEach(question => {
        const urlToEncode = `${ServerConnect.api_url}${question.file[0].file_field}`
        quizPromises.push(imageUrlToBase64(urlToEncode).then(data=>question.file[0].file_field = data))
      });

      let backgrounds = {
        loadBg: `${ServerConnect.api_url}${data.find(item=>item.name === 'load').file[0].file_field}`,
        startBg: `${ServerConnect.api_url}${data.find(item=>item.name === 'start').file[0].file_field}`,
        result1Bg: `${ServerConnect.api_url}${data.find(item=>item.name === 'result1').file[0].file_field}`,
        result2Bg: `${ServerConnect.api_url}${data.find(item=>item.name === 'result2').file[0].file_field}`,
        result3Bg: `${ServerConnect.api_url}${data.find(item=>item.name === 'result3').file[0].file_field}`,
        finalBg: `${ServerConnect.api_url}${data.find(item=>item.name === 'final').file[0].file_field}`
      }

      bgPromises.push(
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'load').file[0].file_field}`).then(data=>backgrounds.loadBg = data),
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'start').file[0].file_field}`).then(data=>backgrounds.startBg = data),
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'result1').file[0].file_field}`).then(data=>backgrounds.result1Bg = data),
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'result2').file[0].file_field}`).then(data=>backgrounds.result2Bg = data),
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'result3').file[0].file_field}`).then(data=>backgrounds.result3Bg = data),
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'final').file[0].file_field}`).then(data=>backgrounds.finalBg = data)
      )

      const info = {
        start: {
            text: data.find(item=>item.name === 'start').text[0].title,
            description: data.find(item=>item.name === 'start').text[0].text
        },
        final: {
          text1: data.find(item=>item.name === 'result1').text[0].title,
          description1: data.find(item=>item.name === 'result1').text[0].text,
          text2: data.find(item=>item.name === 'result2').text[0].title,
          description2: data.find(item=>item.name === 'result2').text[0].text,
          text3: data.find(item=>item.name === 'result3').text[0].title,
          description3: data.find(item=>item.name === 'result3').text[0].text
        },
        compet: {
            text: data.find(item=>item.name === 'final').text[0].title,
            description: data.find(item=>item.name === 'final').text[0].text
        },
        modal: {
            loosePic: `${ServerConnect.api_url}${data.find(item=>item.name === 'modal').file[1].file_field}`,
            winPic: `${ServerConnect.api_url}${data.find(item=>item.name === 'modal').file[0].file_field}`
        }
      }

      infoPromises.push(
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'modal').file[1].file_field}`).then(data=>info.modal.loosePic = data),
        imageUrlToBase64(`${ServerConnect.api_url}${data.find(item=>item.name === 'modal').file[0].file_field}`).then(data=>info.modal.winPic = data),
      )

      Promise.all(quizPromises).then(() =>
        dispatch(setQuiz(quiz))
      );

      Promise.all(bgPromises).then(() =>
        dispatch(setBackgrounds(backgrounds))
      );

      Promise.all(infoPromises).then(() =>
        dispatch(setInfo(info))
      );

    })
    .catch(error=>console.log(error))

    let app = window.Telegram.WebApp
    let query = app.initData
    let user_data_str = parseQuery(query).user
    let user_data = JSON.parse(user_data_str)

    dispatch(setUserID(user_data.id))

    app.expand()
    app.ready()

  }, [])

  const LooseModal = () => {
    return (
      <div className="modal loose-modal" onClick={(e)=>{e.stopPropagation();}}>
        <img className="modal__pic" src={`${answerIsRight ? info.modal.winPic : info.modal.loosePic}`} width="112"/>
        <Title className="loose-modal__title" size={'big'} color={'black'}>
          {
            answerIsRight ? quiz[quizIndex].question[0].text_win_title : quiz[quizIndex].question[0].text_lose_title
          }
        </Title>
        <Text className="loose-modal__text" size={'normal'} color={'black'}>
          {
            answerIsRight ? quiz[quizIndex].question[0].text_win : quiz[quizIndex].question[0].text_lose
          }
        </Text>
        <div className="loose-modal__buttons">
          <Button size={'small'} color={'red'} onClick={goToNextQuestion}>Продолжить</Button>
        </div>
      </div>
    )
  }

  let content = []
  if (activePanel === 'loading') {
    content.push(<Loading key='loading'/>)
  } else if (activePanel === 'main') {
    content.push(<Main key='main' sendStat={sendStat}/>)
  } else if (activePanel === 'start') {
    content.push(<Start key='start'/>)
  } else if (activePanel === 'final') {
    content.push(<Final key='final' sendStat={sendStat}/>)
  } else if (activePanel === 'compet') {
    content.push(<Compet key='compet' sendStat={sendStat}/>)
  }

  return (
    <div className={`app`}>
      <>
        {content}
        <Overlay>
          {
            activeModal === 'loose' && <LooseModal />
          }
        </Overlay>
      </>
    </div>
  );
}
export default App;
