import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import './style.css';
import Title from "../Title";
import Text from "../Text";
import Button from '../Button';
import { setActiveModal, setAnswerIsRight, setAnswersCounter } from '../../store/mainReducer';
import ServerConnect from '../../service';

const Main = ({sendStat}) => {

    const dispatch = useDispatch()
    const answersCounter = useSelector(state => state.main.answersCounter)
    const quiz = useSelector(state => state.main.quiz)
    const quizIndex = useSelector(state => state.main.quizIndex)
    const quizLength = quiz.length
    const [randomNumber, setRandomNumber] = useState(0)

    function getRandomZeroToOne() {
        return (Math.random()>=0.5)? 1 : 0;
    }

    const checkAnswer = (id)=> {

        if(quiz[quizIndex].question[0].answer === id){
            dispatch(setAnswersCounter(answersCounter+1))
            dispatch(setAnswerIsRight(true))
            dispatch(setActiveModal('loose'))

        } else {
            dispatch(setAnswerIsRight(false))
            dispatch(setActiveModal('loose'))

        }
    }

    useEffect(()=>{
        setRandomNumber(getRandomZeroToOne())
    },[quizIndex])

    return (
        <div className={`main-page`} style={{backgroundImage: `url(${quiz[quizIndex].file[0].file_field})`}}>
            <p className="main-page__counter">
                {quizIndex+1}/{quizLength}
            </p>
            <div className="main-content">
                <Title className="main-content__title" size={'normal'} color={'black'}>{quiz[quizIndex].question[0].text}</Title>
                <div className="main-content__buttons">
                    {
                        randomNumber === 0 ?
                        <>
                            <Button size="small" color="white" onClick={()=>checkAnswer(1)}>
                                {quiz[quizIndex].question[0].choice1}
                            </Button>
                            <Button size="small" color="white" onClick={()=>checkAnswer(2)}>
                                {quiz[quizIndex].question[0].choice2}
                            </Button>
                        </> :
                        <>
                            <Button size="small" color="white" onClick={()=>checkAnswer(2)}>
                                {quiz[quizIndex].question[0].choice2}
                            </Button>
                            <Button size="small" color="white" onClick={()=>checkAnswer(1)}>
                                {quiz[quizIndex].question[0].choice1}
                            </Button>
                        </>
                    }
                </div>
            </div>
        </div>
    );
}

export default Main;

