import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import Button from "../Button";
import Text from "../Text";
import Title from "../Title";
import './style.css';

const Compet = ({sendStat}) => {

    const backgrounds = useSelector(state => state.main.backgrounds)
    const info = useSelector(state => state.main.info)

    useEffect(()=>{
        sendStat('final')
    },[])

    if(backgrounds){
    return (
        <div className="compet" style={{backgroundImage: `url(${backgrounds.finalBg})`}}>
            {/* <img src="assets/images/logo-white.svg" width={150} alt="" className="compet__icon" /> */}
            <div className="compet__content">
                <Title size={'big'} color={'white'} className={'compet__title'}>
                    {info.compet.text}
                </Title>
                <Text size={'big'} color={'white'} className={'compet__text'}>
                    {info.compet.description}
                </Text>
                <Button size="small" color="white" className={'compet__button'} href="https://t.me/Spb3355555">
                    Канал «Петербургской Недвижимости»
                </Button>
            </div>
        </div>
    )}
}

export default Compet;