import React from "react";
import './style.css'
import Title from "../Title";
import Text from "../Text";
import Button from "../Button";
import { setActivePanel } from "../../store/mainReducer";
import { useDispatch, useSelector } from "react-redux";

const Start = ({}) => {

    const dispatch = useDispatch()
    const backgrounds = useSelector(state => state.main.backgrounds)
    const info = useSelector(state => state.main.info)

    return (
        <div className="start container" style={{backgroundImage: `url(${backgrounds.startBg})`}}>
            <img src={backgrounds.startBg} alt="" />
            <Title className="start__title" size={'normal'} color={'white'}>{info.start.text}</Title>
            <Text className="start__text" size={'normal'} color={'white'}>{info.start.description}</Text>
            <div className="start__buttons">
                <Button size="small" color="white" onClick={()=>dispatch(setActivePanel('main'))}>Продолжить</Button>
                {/* <Button size="small" color="transparent">Правила игры</Button> */}
            </div>
        </div>
    );
}

export default Start;