import {createSlice} from "@reduxjs/toolkit";

const mainSlice = createSlice({
    name: 'main',
    initialState: {
        platform: null,
        activePanel: 'loading',
        history: ['loading'],
        isLoading: false,
        activeModal: null,
        resultPic: null,
        quizIndex: 0,
        answersCounter: 0,
        answerIsRight: false,
        loadingBg: null,
        userID: null,
        info: {
            start: {
                text: null,
                description: null
            },
            final: {
                text1: null,
                description1: null,
                text2: null,
                description2: null,
                text3: null,
                description3: null
            },
            compet: {
                text: null,
                description: null
            },
            modal: {
                loosePic: null,
                winPic: null
            }
        },
        backgrounds: {
            loadBg: null,
            startBg: null,
            result1Bg: null,
            result2Bg: null,
            result3Bg: null,
            finalBg: null
        },
        quiz: null,
        currentQuizID: 1,
    },
    reducers: {
        setActivePanel(state, action) {
            state.activePanel = action.payload
            state.history = [...state.history, action.payload]
            // window.history.pushState({panel: state.activePanel}, 'start')
        },
        setQuiz(state, action){
            state.quiz = action.payload
        },
        setInfo(state, action){
            state.info = action.payload
        }
        ,
        setActiveModal(state, action) {
            state.activeModal = action.payload
        },
        setMessageModalTitle(state, action){
            state.messageModalTitle = action.payload
        },
        setMessageModalText(state, action){
            state.messageModalText = action.payload
        },
        setIsLoading(state, action){
            state.isLoading = action.payload
        },
        setQuizIndex(state, action){
            state.quizIndex = action.payload
        },
        setAnswersCounter(state, action){
            state.answersCounter = action.payload
        },
        setAnswerIsRight(state, action){
            state.answerIsRight = action.payload
        },
        setBackgrounds(state, action){
            state.backgrounds = action.payload
        },
        setUserID(state, action){
            state.userID = action.payload
        },
    }
})

export default mainSlice.reducer
export const {
    setActivePanel,
    setActiveModal,
    setIsLoading,
    setMessageModalTitle,
    setMessageModalText,
    setQuizIndex,
    setAnswersCounter,
    setAnswerIsRight,
    setQuiz,
    setBackgrounds,
    setInfo,
    setUserID
} = mainSlice.actions

