import React from "react";
import './style.css'

const Button = ({size, onClick, color, children, className, href}) => {

    if (href){
        return (
            <a href={href} className={`button button_size_${size} button_color_${color} ${className}`} target="_blank">
                {children}
            </a>
        );
    } else {
        return (
            <button className={`button button_size_${size} button_color_${color} ${className}`} onClick={onClick}>
                {children}
            </button>
        );
    }
}

export default Button;