import React, { useEffect } from 'react'
import { useDispatch, useSelector } from "react-redux"
import { setActivePanel } from "../../store/mainReducer"
import './style.css'

const Loading = () => {
    const dispatch = useDispatch()
    const backgrounds = useSelector(state => state.main.backgrounds)

    useEffect(() => {

        const loading = setTimeout(() => {
            dispatch(setActivePanel('start'))
        }, 2500);
        return () => clearTimeout(loading);

    }, []);


    return (
        <div className="container loading-page" style={{backgroundImage: `url(${backgrounds.loadBg})`}}>
            {/* <img className="loading-page__company-logo" width="241" height="54" src="assets/images/logo-white.svg" loading="lazy" decoding="async" alt="logo" /> */}
        </div>

    )

};



export default Loading;