import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Text from "../Text";
import Title from "../Title";
import './style.css';
import Button from "../Button";
import { setActivePanel } from "../../store/mainReducer";

const Final = ({sendStat}) => {

    const dispatch = useDispatch()
    const answersCounter = useSelector(state => state.main.answersCounter)
    const backgrounds = useSelector(state => state.main.backgrounds)
    const info = useSelector(state => state.main.info)

    if(backgrounds){
    return (
        <div className="final" style={{backgroundImage: `url(${answersCounter < 4 ? backgrounds.result1Bg : answersCounter < 10 ? backgrounds.result2Bg : backgrounds.result3Bg})`}}>
            <div className="final__content">
                <Title size={'normal'} color={'white'} className={'final__title'}>
                    {
                        answersCounter < 4 ? info.final.text1 : answersCounter < 10 ? info.final.text2 : info.final.text3
                    }
                </Title>
                <Text size={'normal'} color={'white'} className={'final__text'}>
                    {
                        answersCounter < 4 ? info.final.description1 : answersCounter < 10 ? info.final.description2 : info.final.description3
                    }
                </Text>
                <Button size="small" color="white" className={'final__button'} onClick={()=>dispatch(setActivePanel('compet'))}>
                    Принять участие в розыгрыше
                </Button>
            </div>
        </div>
    )}
}

export default Final;